<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">发布帖子</p>
        </div>
        <div class="setBox">
            <div class="setTitle pl-30 f28 c333">
                标题
                <input type="text" v-model="title" class="oinp" placeholder="请输入">
            </div>
            <div class="home-comntent">
                <p class="pl-30 f28 c333 pt-20 pb-30">内容</p>
                <textarea name="" id="" cols="30" class="textarea f26 pl-30 pr-30 pt-10" v-model="topicDesc" rows="10" placeholder="请输入帖子内容"></textarea>
            </div>
            <div class="upload">
                <p class="pl-30 f28 c333 pt-20 pb-30">图片</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in imageUrl" :key="index">
                        <div class="imgShow">
                            <img :src="$api.fileUrl + item" alt=""  @click="show(item)">
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox" v-if="imageUrl.length < 10">
                        <van-uploader :after-read="onRead" accept="image/png, image/jpeg">
                            <img class="imgShow" src="../../../assets/my/upload.png" alt="">
                        </van-uploader>
                    </div>
                </div>
            </div>
            <div class="upload mt-30">
                <p class="pl-30 f28 c333 pt-20 pb-30">视频</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in videoUrl" :key="index">
                        <div class="imgShow">
                            <video :src="$api.fileUrl + item" alt=""></video>
                            <span @click="deleteThis" class="closeX f20">X</span>
                        </div>
                    </div>
                    <div class="imgShowBox" v-if="videoUrl.length < 1">
                        <van-uploader :after-read="onReadV" accept="video/*">
                            <img class="imgShow" src="../../../assets/my/upload.png" alt="">
                        </van-uploader>
                    </div>
                </div>
            </div>
            <div style="height:2rem;"></div>
            <div class="centerBtn f26 cfff text-center" @click="insertSchoolTopic">发布</div>
        </div>
    </div>
</template>

<script>
import { ImagePreview } from 'vant';
export default {
  name: 'HelloWorld',
    data () {
        return {
            title : '',
            topicDesc : '',
            imageUrl :[],
            videoUrl : []
        }
    },
    methods : {
       returns(){
            this.$router.push({path:'/schoolcircle'});//返回上一层
        },
        deleteThis(){
            alert('删除')
        },
        show(url){
            ImagePreview([
                this.$api.fileUrl + url
            ]);
        },
        onRead(file) {//上传图片
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.imageUrl.push(data.data[0]);
                }
            })
        },
        onReadV(file){
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.videoUrl.push(data.data[0]);
                }
            })
        },
        insertSchoolTopic(){
            if(this.title == ''){
                this.$toast('请输入标题');
                return
            }
            if(this.topicDesc == ''){
                this.$toast('请输入内容');
                return
            }
            this.$toast.loading({
                mask: true,
                message: '加载中...',
                duration:0
            });
            var param = {
                classesId : JSON.parse(localStorage.getItem('zhxy_user')).classesId,
                title : this.title,
                topicDesc : this.topicDesc,
                submitId : JSON.parse(localStorage.getItem('zhxy_user')).id,
                submitType : 2,
                state : 2,
                imageUrl : this.imageUrl.toString(),
                videoUrl : this.videoUrl.toString(),
                schoolId : JSON.parse(localStorage.getItem('zhxy_user')).schoolId
            };
            this.$http({url:this.$api.class.classInsertSchoolTopic},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$toast('发布成功');
                    this.$router.push({path:'/schoolcircle'});
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }
    },
    created() {
        
    },
}
</script>

<style scoped>
.setBox{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-y: auto;}
.setTitle{height: 1.11rem;border-bottom: 1px solid #f2f2f2;line-height: 1.11rem;overflow: hidden;}
.oinp{float: right;border: 0;outline: 0;width: 80%;text-align: right;padding-right: 0.2rem}
.home-comntent{height: 3.74rem;border-bottom: 1px solid #f2f2f2;}
.textarea{width: 100%;height: 1.1rem;outline: 0;border: 0;box-sizing: border-box;resize:none;}
.iploadBox{margin-left: 0.3rem;overflow: hidden;}
.imgShowBox{width: 20%;float: left;}
.imgShow{width: 1.2rem;height: 1.2rem;border-radius: 5px;overflow: hidden;float: left;position: relative;}
.imgShow img{width: 100%;height: 100%;display: block;}
.imgShow video{width: 100%;height: 100%;display: block;}
.closeX{position: absolute;top: 0;right: 0;padding: 0.05rem 0.1rem;}
.borderTop{border-top: 1px solid #f2f2f2;}
.msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
.centerBtn{height: 0.9rem;width: 5rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -2.5rem; }

</style>





